"use client";
import Link from "next/link";
import React, { useState } from "react";

const navItems = [
  { name: "Home", path: "/" },
  { name: "Horoscopes", path: "/horoscope" },
  { name: "Birth Chart", path: "/birth-chart" },
  { name: "Compatibility", path: "/compatibility" },
  { name: "Planets", path: "/planetary" },
  { name: "Vedic Astrology", path: "/vedic" },
  { name: "Vastu", path: "/vastu" },
  { name: "Contact", path: "/contact" },
];

export default function ShivaNavbar() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <nav className="bg-black/30 backdrop-blur-md border-b border-purple-500/20 sticky top-0 z-50">
      <div className="container mx-auto px-4">
        <div className="flex justify-between items-center py-4">
          <Link href="/" className="text-2xl font-bold text-yellow-400" style={{ fontFamily: 'Cinzel, serif' }}>
            ‡•ê Om Namah Shivaya
          </Link>
          
          {/* Desktop Menu */}
          <ul className="hidden md:flex space-x-6">
            {navItems.map((item) => (
              <li key={item.path}>
                <Link
                  href={item.path}
                  className="text-white hover:text-yellow-400 transition-colors duration-300 font-medium"
                >
                  {item.name}
                </Link>
              </li>
            ))}
          </ul>

          {/* Mobile Menu Button */}
          <button
            className="md:hidden text-white"
            onClick={() => setIsOpen(!isOpen)}
          >
            <div className="w-6 h-6 flex flex-col justify-center space-y-1">
              <div className={`h-0.5 bg-white transition-all ${isOpen ? 'rotate-45 translate-y-1.5' : ''}`}></div>
              <div className={`h-0.5 bg-white transition-all ${isOpen ? 'opacity-0' : ''}`}></div>
              <div className={`h-0.5 bg-white transition-all ${isOpen ? '-rotate-45 -translate-y-1.5' : ''}`}></div>
            </div>
          </button>
        </div>

        {/* Mobile Menu */}
        {isOpen && (
          <div className="md:hidden pb-4">
            <ul className="space-y-2">
              {navItems.map((item) => (
                <li key={item.path}>
                  <Link
                    href={item.path}
                    className="block text-white hover:text-yellow-400 transition-colors duration-300 py-2"
                    onClick={() => setIsOpen(false)}
                  >
                    {item.name}
                  </Link>
                </li>
              ))}
            </ul>
          </div>
        )}
      </div>
    </nav>
  );
}
